/*--------------------------------------------------------------------------------------------
*
*  Mixins
*
*--------------------------------------------------------------------------------------------*/
/*--------------------------------------------------------------------------------------------
*
*  Global
*
*--------------------------------------------------------------------------------------------*/
/* Horizontal List */
.acf-hl {
  padding: 0;
  margin: 0;
  list-style: none;
  display: block;
  position: relative;
}

.acf-hl > li {
  float: left;
  display: block;
  margin: 0;
  padding: 0;
}

.acf-hl > li.acf-fr {
  float: right;
}

/* Horizontal List: Clearfix */
.acf-hl:before,
.acf-hl:after,
.acf-bl:before,
.acf-bl:after,
.acf-cf:before,
.acf-cf:after {
  content: "";
  display: block;
  line-height: 0;
}

.acf-hl:after,
.acf-bl:after,
.acf-cf:after {
  clear: both;
}

/* Block List */
.acf-bl {
  padding: 0;
  margin: 0;
  list-style: none;
  display: block;
  position: relative;
}

.acf-bl > li {
  display: block;
  margin: 0;
  padding: 0;
  float: none;
}

/* Visibility */
.acf-hidden {
  display: none !important;
}

.acf-empty {
  display: table-cell !important;
}
.acf-empty * {
  display: none !important;
}

/* Float */
.acf-fl {
  float: left;
}

.acf-fr {
  float: right;
}

.acf-fn {
  float: none;
}

/* Align */
.acf-al {
  text-align: left;
}

.acf-ar {
  text-align: right;
}

.acf-ac {
  text-align: center;
}

/* loading */
.acf-loading,
.acf-spinner {
  display: inline-block;
  height: 20px;
  width: 20px;
  vertical-align: text-top;
  background: transparent url(../../images/spinner.gif) no-repeat 50% 50%;
}

/* spinner */
.acf-spinner {
  display: none;
}

.acf-spinner.is-active {
  display: inline-block;
}

/* WP < 4.2 */
.spinner.is-active {
  display: inline-block;
}

/* required */
.acf-required {
  color: #f00;
}

/* Allow pointer events in reusable blocks */
.acf-button,
.acf-tab-button {
  pointer-events: auto !important;
}

/* show on hover */
.acf-soh .acf-soh-target {
  -webkit-transition: opacity 0.25s 0s ease-in-out, visibility 0s linear 0.25s;
  -moz-transition: opacity 0.25s 0s ease-in-out, visibility 0s linear 0.25s;
  -o-transition: opacity 0.25s 0s ease-in-out, visibility 0s linear 0.25s;
  transition: opacity 0.25s 0s ease-in-out, visibility 0s linear 0.25s;
  visibility: hidden;
  opacity: 0;
}

.acf-soh:hover .acf-soh-target {
  -webkit-transition-delay: 0s;
  -moz-transition-delay: 0s;
  -o-transition-delay: 0s;
  transition-delay: 0s;
  visibility: visible;
  opacity: 1;
}

/* show if value */
.show-if-value {
  display: none;
}

.hide-if-value {
  display: block;
}

.has-value .show-if-value {
  display: block;
}

.has-value .hide-if-value {
  display: none;
}

/* select2 WP animation fix */
.select2-search-choice-close {
  -webkit-transition: none;
  -moz-transition: none;
  -o-transition: none;
  transition: none;
}

/*---------------------------------------------------------------------------------------------
*
*  tooltip
*
*---------------------------------------------------------------------------------------------*/
/* tooltip */
.acf-tooltip {
  background: #1D2939;
  border-radius: 6px;
  color: #D0D5DD;
  padding-top: 8px;
  padding-right: 12px;
  padding-bottom: 10px;
  padding-left: 12px;
  position: absolute;
  z-index: 900000;
  max-width: 280px;
  box-shadow: 0px 12px 16px -4px rgba(16, 24, 40, 0.08), 0px 4px 6px -2px rgba(16, 24, 40, 0.03);
  /* tip */
  /* positions */
}
.acf-tooltip:before {
  border: solid;
  border-color: transparent;
  border-width: 6px;
  content: "";
  position: absolute;
}
.acf-tooltip.top {
  margin-top: -8px;
}
.acf-tooltip.top:before {
  top: 100%;
  left: 50%;
  margin-left: -6px;
  border-top-color: #2f353e;
  border-bottom-width: 0;
}
