/*--------------------------------------------------------------------------------------------
*
*	acf-icon
*
*--------------------------------------------------------------------------------------------*/
.acf-icon {
  display: inline-block;
  height: 28px;
  width: 28px;
  border: transparent solid 1px;
  border-radius: 100%;
  font-size: 20px;
  line-height: 21px;
  text-align: center;
  text-decoration: none;
  vertical-align: top;
  box-sizing: border-box;
}
.acf-icon:before {
  font-family: dashicons;
  display: inline-block;
  line-height: 1;
  font-weight: 400;
  font-style: normal;
  speak: none;
  text-decoration: inherit;
  text-transform: none;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 1em;
  height: 1em;
  vertical-align: middle;
  text-align: center;
}

.acf-icon.-plus:before {
  content: "\f543";
}

.acf-icon.-minus:before {
  content: "\f460";
}

.acf-icon.-cancel:before {
  content: "\f335";
  margin: -1px 0 0 -1px;
}

.acf-icon.-pencil:before {
  content: "\f464";
}

.acf-icon.-location:before {
  content: "\f230";
}

.acf-icon.-up:before {
  content: "\f343";
  margin-top: -0.1em;
}

.acf-icon.-down:before {
  content: "\f347";
  margin-top: 0.1em;
}

.acf-icon.-left:before {
  content: "\f341";
  margin-left: -0.1em;
}

.acf-icon.-right:before {
  content: "\f345";
  margin-left: 0.1em;
}

.acf-icon.-sync:before {
  content: "\f463";
}

.acf-icon.-globe:before {
  content: "\f319";
  margin-top: 0.1em;
  margin-left: 0.1em;
}

.acf-icon.-picture:before {
  content: "\f128";
}

.acf-icon.-check:before {
  content: "\f147";
  margin-left: -0.1em;
}

.acf-icon.-dot-3:before {
  content: "\f533";
  margin-top: -0.1em;
}

.acf-icon.-arrow-combo:before {
  content: "\f156";
}

.acf-icon.-arrow-up:before {
  content: "\f142";
  margin-left: -0.1em;
}

.acf-icon.-arrow-down:before {
  content: "\f140";
  margin-left: -0.1em;
}

.acf-icon.-search:before {
  content: "\f179";
}

.acf-icon.-link-ext:before {
  content: "\f504";
}

.acf-icon.-duplicate {
  position: relative;
}
.acf-icon.-duplicate:before, .acf-icon.-duplicate:after {
  content: "";
  display: block;
  box-sizing: border-box;
  width: 46%;
  height: 46%;
  position: absolute;
  top: 33%;
  left: 23%;
}
.acf-icon.-duplicate:before {
  margin: -1px 0 0 1px;
  box-shadow: 2px -2px 0px 0px currentColor;
}
.acf-icon.-duplicate:after {
  border: solid 2px currentColor;
}

.acf-icon.-trash {
  position: relative;
}
.acf-icon.-trash:before, .acf-icon.-trash:after {
  content: "";
  display: block;
  box-sizing: border-box;
  width: 46%;
  height: 46%;
  position: absolute;
  top: 33%;
  left: 23%;
}
.acf-icon.-trash:before {
  margin: -1px 0 0 1px;
  box-shadow: 2px -2px 0px 0px currentColor;
}
.acf-icon.-trash:after {
  border: solid 2px currentColor;
}

.acf-icon.-collapse:before {
  content: "\f142";
  margin-left: -0.1em;
}

.-collapsed .acf-icon.-collapse:before {
  content: "\f140";
  margin-left: -0.1em;
}

span.acf-icon {
  color: #555d66;
  border-color: #b5bcc2;
  background-color: #fff;
}

a.acf-icon {
  color: #555d66;
  border-color: #b5bcc2;
  background-color: #fff;
  position: relative;
  transition: none;
  cursor: pointer;
}
a.acf-icon:hover {
  background: #f3f5f6;
  border-color: #0071a1;
  color: #0071a1;
}
a.acf-icon.-minus:hover, a.acf-icon.-cancel:hover {
  background: #f7efef;
  border-color: #a10000;
  color: #dc3232;
}
a.acf-icon:active, a.acf-icon:focus {
  outline: none;
  box-shadow: none;
}

.acf-icon.-clear {
  border-color: transparent;
  background: transparent;
  color: #444;
}

.acf-icon.light {
  border-color: transparent;
  background: #f5f5f5;
  color: #23282d;
}

.acf-icon.dark {
  border-color: transparent !important;
  background: #23282d;
  color: #eee;
}

a.acf-icon.dark:hover {
  background: #191e23;
  color: #00b9eb;
}
a.acf-icon.dark.-minus:hover, a.acf-icon.dark.-cancel:hover {
  color: #d54e21;
}

.acf-icon.grey {
  border-color: transparent !important;
  background: #b4b9be;
  color: #fff !important;
}
.acf-icon.grey:hover {
  background: #00a0d2;
  color: #fff;
}
.acf-icon.grey.-minus:hover, .acf-icon.grey.-cancel:hover {
  background: #32373c;
}

.acf-icon.small,
.acf-icon.-small {
  width: 20px;
  height: 20px;
  line-height: 14px;
  font-size: 14px;
}
.acf-icon.small.-duplicate:before, .acf-icon.small.-duplicate:after,
.acf-icon.-small.-duplicate:before,
.acf-icon.-small.-duplicate:after {
  opacity: 0.8;
}

.acf-icon.-plus-alt {
    -webkit-mask-image: url("../../images/icons/icon-add-alt.svg");
    mask-image: url("../../images/icons/icon-add-alt.svg")
}

.acf-icon.-duplicate-alt {
    -webkit-mask-image: url("../../images/icons/icon-duplicate.svg");
    mask-image: url("../../images/icons/icon-duplicate.svg")
}

.acf-icon.-trash-alt {
